import * as types from './mutation-types'
import loginApi from './api/login'
import bus from './modules/bus/index'
import store from './index'
import router from '../routers/index'

export function initVuexModules (store : any) {
  !store.state.bus && store.registerModule('bus', bus)
}


export const login = ({ commit, state, rootState, dispatch } : any, data: any) => {
  return loginApi.login(data).then((res: any) => {
    if (res.success) {
      if (res.sessionId) {
        commit(types.SET_TOKEN, res.sessionId)
        initVuexModules(store)
        router.replace('home')
        return { success: true }
      } else {
        return { success: false }
      }
    } else {
      return { success: false }
    }
  })
}

