import send from './http'
const LOGIN = 'login'
const GETCODEINFO = 'getCodeInfo'
export default {
  login (data : any) {
    let req = {
      data: data,
      action: LOGIN
    }
    return send.fetch(req, 'post')
  },
  getCodeInfo (data: any) {
    let req = {
      data: data,
      action: GETCODEINFO
    }
    return send.fetch(req, 'post')
  },
  bindingOpenid (data: any) {
    let req = {
      data: data,
      action: 'bindingOpenid'
    }
    return send.fetch(req, 'post')
  },
  getHouseList (data: any) {
    let req = {
      data: data,
      action: 'app.getHouseList'
    }
    return send.fetch(req, 'post')
  }
}
